@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
.side-drawer {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    height: 100vh;
    width: 70%;
    background: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  }
  
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.main-page {
  width: 100%;
  height: 10vh;
  margin: 0;
  outline: 0;
  background-color: #59bdc5;
}
.main-page h1 {
  font-family: "Roboto", sans-serif;
  width: 50%;
  color: #6c7070;
  text-align: left;
  font-size: 2rem;
  font-weight: bold;
  margin-left: 20px;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.main-page img {
  border-radius: 100%;
  height: 8vh;
  position: absolute;
  top: 1vh;
  right: 10px;
}

.main-page_btn {
  padding: 0;
  border: none;
  background: none;
}

.menu-bar {
  width: 100%;
  height: 8vh;
  text-align: center;
  background-color: rgb(236, 100, 46);
  font-family: "Roboto";
}

.chats,
.friends,
.posts {
  float: left;
  width: 33.33%;
  height: 100%;
}

.chats {
  background-color: hotpink;
}

.friends {
  background-color: lightskyblue;
}

.posts {
  background-color: lightsalmon;
}

.menu-bar p {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.menu-bar a {
  text-decoration: none;
  color: inherit;
}

.card {
  position: relative;
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  padding: 1rem;
  overflow: hidden;
  background: white;
}

.form-control {
    margin: 1rem 0;
  }
  
  .form-control label,
  .form-control input,
  .form-control textarea {
    display: block;
  }
  
  .form-control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .form-control input,
  .form-control textarea {
    width: 100%;
    font: inherit;
    border: 1px solid #ccc;
    background: #f8f8f8;
    padding: 0.15rem 0.25rem;
  }
  
  .form-control input:focus,
  .form-control textarea:focus {
    outline: none;
    background: #ebebeb;
    border-color: #510077;
  }
  
  .form-control--invalid label,
  .form-control--invalid p {
    color: red;
  }
  
  .form-control--invalid input,
  .form-control--invalid textarea {
    border-color: red;
    background: #ffd1d1;
  }
.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #ff0055;
  border-radius: 4px;
  background: #ff0055;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #ff4382;
  border-color: #ff4382;
}

.button--inverse {
  background: transparent;
  color: #ff0055;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
.modal {
    z-index: 100;
    position: fixed;
    top: 22vh;
    left: 10%;
    width: 80%;
    background: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 8px;
  }
  
  .modal__header {
    width: 100%;
    padding: 1rem 0.5rem;
    background: #2a006e;
    color: white;
  }
  
  .modal__header h2 {
    margin: 0.5rem;
  }
  
  .modal__content {
    padding: 1rem 0.5rem;
  }
  
  .modal__footer {
    padding: 1rem 0.5rem;
  }
  
  @media (min-width: 768px) {
    .modal {
      left: calc(50% - 20rem);
      width: 40rem;
    }
  }
  
  .modal-enter {
    transform: translateY(-10rem);
    opacity: 0;
  }
  
  .modal-enter-active {
    transform: translateY(0);
    opacity: 1;
    transition: all 200ms;
  }
  
  .modal-exit {
    transform: translateY(0);
    opacity: 1;
  }
  
  .modal-exit-active {
    transform: translateY(-10rem);
    opacity: 0;
    transition: all 200ms;
  }
  
.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #510077;
  border-color: #510077 transparent #510077 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.authentication__header {
    color: white;
    text-align: center;
  }
  
  .authentication {
    width: 90%;
    max-width: 25rem;
    margin: 7rem auto;
    text-align: center;
  }
  
  .authentication form {
    margin-bottom: 1rem;
  }
.all-chat {
  background-color: lightsteelblue;
  height: 50vh;
  text-align: left;
  width: 100%; 
}

.chat-box {
  height: auto;
  overflow: auto;
  padding: 20px;
  background-color: lightyellow;
}

.friend-image {
  width: 8vh;
  height: 8vh;
  margin-right: 15px;
  border-radius: 100%;
  border: 2px solid black;
  float: left;
}
.friend-info {
  height: 8vh;
  overflow-y: hidden;
}

.friend-name {
  height: 50%;
  font-size: 1rem;
}

.friend-msg {
  height: 50%;
  line-height: 1.6;
}

.friend-info-a{
  text-decoration: none;
  color: inherit;
}

.messenger-box {
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background-color: lightsteelblue;
}

.messenger-header {
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
  margin: 0;
  outline: 0;
  overflow: auto;
  background-color: lightyellow;
  text-align: left;
}

.messenger-header button {
  width: 5vh;
  height: 5vh;
  font-size: 1.5rem;
  background-color: grey;
  margin: 10px;
  float: left;
}

.messenger-header div {
  height: 8vh;
}

.messenger-header h1 {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  float: left;
  font-size: 1.5rem;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.backButton p{
  color: inherit;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.messages {
  position: relative;
  box-sizing: border-box;
  padding: 20px;
  width: 100%;
  height: calc(100% - 8vh - 40px);
  overflow: auto;
  background-color: khaki;
}

.textfield {
  width: calc(100% - 40px);
  position: absolute;
  bottom: 20px;
}

.textfield span {
  display: block;
  width: 50px;
  height: 50px;
  margin-right: 1%;
  text-align: center;
  float: left;
  background-color: grey;
}

.textfield span p {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.textfield-form input {
  float: left;
  width: calc(96% - 170px);
  padding: 0 10px;
  border: none;
  margin-right: 1%;
  height: 50px;
  background-color: white;
}

.textfield-form button{
  display: none;
}

.all-chats {
  height: calc(100% - 80px);
  margin-bottom: 10px;
  overflow-y: scroll;
  padding: 5px 0;
  background-color: aquamarine;
}

.all-chats div {
  overflow: auto;
}

.left {
  display: inline;
  margin: 10px;
  padding: 5px 10px;
  white-space: pre-line;
  float: left;
  background-color: lightpink;
}

.right {
  display: inline;
  margin: 10px;
  padding: 5px 10px;
  white-space: pre-line;
  float: right;
  background-color: lightsalmon;
}


