.main-page {
  width: 100%;
  height: 10vh;
  margin: 0;
  outline: 0;
  background-color: #59bdc5;
}
.main-page h1 {
  font-family: "Roboto", sans-serif;
  width: 50%;
  color: #6c7070;
  text-align: left;
  font-size: 2rem;
  font-weight: bold;
  margin-left: 20px;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.main-page img {
  border-radius: 100%;
  height: 8vh;
  position: absolute;
  top: 1vh;
  right: 10px;
}

.main-page_btn {
  padding: 0;
  border: none;
  background: none;
}
