.messenger-box {
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background-color: lightsteelblue;
}

.messenger-header {
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
  margin: 0;
  outline: 0;
  overflow: auto;
  background-color: lightyellow;
  text-align: left;
}

.messenger-header button {
  width: 5vh;
  height: 5vh;
  font-size: 1.5rem;
  background-color: grey;
  margin: 10px;
  float: left;
}

.messenger-header div {
  height: 8vh;
}

.messenger-header h1 {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  float: left;
  font-size: 1.5rem;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.backButton p{
  color: inherit;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.messages {
  position: relative;
  box-sizing: border-box;
  padding: 20px;
  width: 100%;
  height: calc(100% - 8vh - 40px);
  overflow: auto;
  background-color: khaki;
}

.textfield {
  width: calc(100% - 40px);
  position: absolute;
  bottom: 20px;
}

.textfield span {
  display: block;
  width: 50px;
  height: 50px;
  margin-right: 1%;
  text-align: center;
  float: left;
  background-color: grey;
}

.textfield span p {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.textfield-form input {
  float: left;
  width: calc(96% - 170px);
  padding: 0 10px;
  border: none;
  margin-right: 1%;
  height: 50px;
  background-color: white;
}

.textfield-form button{
  display: none;
}

.all-chats {
  height: calc(100% - 80px);
  margin-bottom: 10px;
  overflow-y: scroll;
  padding: 5px 0;
  background-color: aquamarine;
}

.all-chats div {
  overflow: auto;
}

.left {
  display: inline;
  margin: 10px;
  padding: 5px 10px;
  white-space: pre-line;
  float: left;
  background-color: lightpink;
}

.right {
  display: inline;
  margin: 10px;
  padding: 5px 10px;
  white-space: pre-line;
  float: right;
  background-color: lightsalmon;
}
